.story {
	padding: 9.8rem 0;

	&__main {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 9.8rem;
	}

	&__img {
		border-radius: 2rem;
	}

	&__icons {
		margin-top: 2rem;
		display: flex;
		gap: 2rem;

		.face {
			fill: $color-primary;
		}

		.insta {
			fill: $color-secondary;
		}
	}

	&__icon {
		width: 4rem;
		height: 4rem;
	}
}
