.hero {
	padding: 12rem 0 9.8rem;
	padding-left: 17.5rem;
	background-image: linear-gradient(135deg, rgba($color-primary-light, 0.8), rgba($color-secondary-light, 0.5));
	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
	}

	&__text-box {
		padding-top: 8rem;
		width: 100%;
	}
}
